import React from 'react';
import { graphql } from 'gatsby';

import Layout from '../../components/Layout';
import ProjectCard from '../../components/ProjectCard';
import Empty from '../../components/Empty';

import * as styles from '../../styles/projects.module.css';

export default function ProjectsPage({ data }) {

  const projects = data.allProjects.nodes;

  const projectList = projects.map(({ frontmatter: project}) => (
    <ProjectCard project={project} type="mosaic" key={project.slug}/>
  ));

  return (
    <Layout>
      <div className="topSpacing"></div>
      
      <section className={styles.allProjects}>
        <h2>My Projects</h2>
        <div>
          { projects && projects.length > 0 ? (
            projectList
          ): (
            Empty
          )}
        </div>
      </section>
    </Layout>
  );
}

export const query = graphql`
  query ProjectsPage {
    allProjects: allMarkdownRemark {
      nodes {
        frontmatter {
          title
          stack
          description
          slug
          featured
          color
        }
      }
    }
  }
`